import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AppstateService } from 'src/app/services/appstate.service';
import { User } from 'src/app/entities/user';
import { EntryService } from 'src/app/services/entry.service';
import { Comment } from 'src/app/entities/comment';

@Component({
  selector: 'comment-form',
  templateUrl: './comment-form.component.html',
  styleUrls: ['./comment-form.component.css']
})
export class CommentFormComponent implements OnInit {
  @Input("entry")
  public entry: number;

  @Input("id")
  public id: number;

  @Input("text")
  public text: string;

  @Output("saved") CommentSaved = new EventEmitter();

  constructor(private appstateService: AppstateService, private entryService: EntryService) { }

  ngOnInit(): void {
  }

  public save()
  {
    const user: User = this.appstateService.GetUser();
    if(this.id !== null && this.id !== undefined)
    {
      const newComment: Comment = {
        entry: this.entry,
        id: this.id,
        text: this.text,
        creator: user.userName,
        creatorId: user.id
      }
      this.entryService.upateComment(newComment).subscribe((test)=> {
        console.log(test);
        this.CommentSaved.next(newComment);
      });
    }
    else
    {
      const newComment: Comment = {
        entry: this.entry,
        id:null,
        text: this.text,
        creator: user.userName,
        creatorId: user.id
      }
  
      this.entryService.addComment(newComment).subscribe((test)=> {
        console.log(test);
        this.CommentSaved.next(newComment);
      });
    }    
  }
}
